<template>
  <a-card :bordered="false">
    <table-search :fields="fields" @submit="handleSubmit">
      <a-button style="float:right; margin-right: 24px;margin-top:6px" @click="toEditor">添加案例
      </a-button>
    </table-search>
    <a-table size="default" :rowKey="(data, index) => index" :columns="columns"
      :dataSource="tableData" :loading="loading" :pagination="pagination"
      @change="handleTableChange">
      <template slot="action" slot-scope="_, record">
        <a-button type="link" @click="toppingRecoder(record)">
          {{ record.topStatus === 1 ? '取消置顶' : '置顶' }}
        </a-button>
        <a-button type="link" @click="modifyRecoder(record)">
          编辑
        </a-button>
        <a-button type="link" @click="actionRecoder(record)">
          {{ record.publicStatus === 1 ? '下线' : '发布' }}
        </a-button>
        <a-button type="link" class="danger" @click="deleteRecoder(record)">
          删除
        </a-button>
      </template>
      <template slot="publicStatus" slot-scope="text">
        <span class="is-end" v-if="text == 0">未发布</span>
        <span class="is-info" v-else-if="text == 1">已发布</span>
        <span class="is-no" v-else>已更新</span>
      </template>
    </a-table>
  </a-card>
</template>

<script>
import moment from 'moment';
import { TableSearch } from '@jg/via-ui';
import { reactive, toRefs, onMounted, watch } from '@vue/composition-api';
import { getCaseList, updateCaseStatus, deleteCaseInfo, topCaseStatus } from './api';
import { message, Modal } from 'ant-design-vue';

const columns = [
  {
    title: '客户名称',
    dataIndex: 'clientName',
    width: '110px',
  },
  {
    title: '案例标题',
    dataIndex: 'title',
  },
  {
    title: '创建日期',
    dataIndex: 'createTime',
    customRender: text => moment(text).format('YYYY-MM-DD HH:mm:ss'),
  },
  {
    title: '创建者',
    dataIndex: 'creator',
    width: '120px',
  },
  {
    title: '状态',
    dataIndex: 'publicStatus',
    width: '100px',
    scopedSlots: { customRender: 'publicStatus' },
  },
  {
    title: '操作',
    dataIndex: 'action',
    align: 'center',
    maxWidth: '300px',
    scopedSlots: { customRender: 'action' },
  },
];

export default {
  components: {
    TableSearch,
  },
  name: 'tips-list-content',
  setup(_, { root }) {
    const data = reactive({
      lang: 'zh',
      fields: [
        {
          label: '查询条件',
          type: 'input',
          field: 'keyWords',
          value: '',
          placeholder: '请输入案例标题',
        },
      ],
      tableData: [],
      columns,
      loading: true,
      pagination: {
        total: 0,
        current: 1,
        pageSize: 20,
        pageSizeOptions: ['5', '10', '20', '50', '100'],
        showQuickJumper: false,
        showSizeChanger: true,
      },
    });

    watch(
      () => root.$route.path,
      newVal => {
        data.lang = newVal.endsWith('/zh') ? 'zh' : 'en';
        data.pagination.current = 1;
        request();
      },
    );

    function getParams() {
      const params = {};
      params.currentPage = data.pagination.current;
      params.pageSize = data.pagination.pageSize;
      params.keyWords = data.fields[0].value;
      params.languageType = data.lang === 'zh' ? 'CHINESE' : 'ENGLISH';
      return params;
    }

    function request() {
      const params = getParams();
      data.loading = true;
      getCaseList(params)
        .then(res => {
          if (res.list) {
            data.tableData = res.list;
            data.pagination.total = res.total;
          }
        })
        .finally(() => {
          data.loading = false;
        });
    }

    function handleSubmit() {
      data.pagination.current = 1;
      request();
    }

    function handleTableChange(e) {
      data.pagination = e;
      request();
    }

    function modifyRecoder(record) {
      root.$router.push({
        name: 'docs-case-edit',
        params: {
          id: record.id,
          lang: data.lang,
        },
      });
    }
    function toEditor() {
      root.$router.push({
        name: 'docs-case-create',
        params: {
          lang: data.lang,
        },
      });
    }
    function actionRecoder(record) {
      const publicStatus = record.publicStatus === 0 ? 1 : 0;
      Modal.confirm({
        title: '提示',
        content: `确认${publicStatus === 1 ? '发布' : '下线'}该案例吗?`,
        onOk() {
          return updateCaseStatus({
            id: record.id,
            publicStatus,
          }).then(() => {
            message.success(`${publicStatus === 1 ? '发布' : '下线'}成功`);
            request();
          });
        },
      });
    }

    function toppingRecoder(record) {
      const topStatus = record.topStatus === 0 ? 1 : 0;
      Modal.confirm({
        title: '提示',
        content: `确认${topStatus === 1 ? '置顶' : '取消置顶'}该案例吗?`,
        onOk() {
          return topCaseStatus({
            id: record.id,
            topStatus,
          }).then(() => {
            message.success(`${topStatus === 1 ? '置顶' : '取消置顶'}成功`);
            request();
          });
        },
      });
    }

    function deleteRecoder(record) {
      Modal.confirm({
        title: '确认要删除这条信息吗？',
        content: '您确认要删除这条信息吗，删除后不可恢复',
        onOk() {
          return deleteCaseInfo(record.id).then(() => {
            message.success('删除成功');
            request();
          });
        },
      });
    }
    onMounted(() => {
      request();
    });

    return {
      ...toRefs(data),
      handleSubmit,
      handleTableChange,
      modifyRecoder,
      actionRecoder,
      deleteRecoder,
      toppingRecoder,
      toEditor,
    };
  },
};
</script>

<style lang="less" scoped>
.is-info,
.id-end,
.is-no {
  &::before {
    content: '';
    border-radius: 50%;
    width: 4px;
    height: 4px;
    display: inline-block;
    margin-right: 2px;
    transform: translateY(-2px);
    background: currentColor;
  }
}
.is-info {
  color: @success-color;
}
.is-end {
  color: @disabled-color;
}
.is-no {
  color: @warning-color;
}
</style>
