import request from '@/utils/request';

export function getCaseList(params) {
  return request({
    url: 'official/iportal/client/case/get',
    params,
    method: 'GET',
  });
}

export function updateCaseStatus(info) {
  return request({
    url: 'official/iportal/client/case/change',
    data: info,
    method: 'POST',
  });
}

export function previewCaseInfo(id) {
  return request({
    url: 'official/iportal/client/case/preview/' + id,
    method: 'GET',
  });
}

export function saveCaseInfo(info) {
  return request({
    url: 'official/iportal/client/case/save',
    data: info,
    method: 'POST',
  });
}

export function releaseCaseInfo(info) {
  return request({
    url: 'official/iportal/client/case/release',
    data: info,
    method: 'POST',
  });
}

export function topCaseStatus(info) {
  return request({
    url: 'official/iportal/client/case/top',
    data: info,
    method: 'POST',
  });
}

export function getCaseInfo(id) {
  return request({
    url: 'official/iportal/client/case/' + id,
    method: 'GET',
  });
}

export function deleteCaseInfo(id) {
  return request({
    url: 'official/iportal/client/case/' + id,
    method: 'DELETE',
  });
}
